
import { useAuthStore } from '@/store/auth';
import { computed, defineComponent, reactive } from 'vue'
import { useRouter } from 'vue-router';

export default defineComponent({
    setup() {
        const auth = useAuthStore();
        const router = useRouter();
        const registerForm = reactive({
            name: null,
            email: null,
            password: null,
            password_confirmation: null
        });

        const errors = computed(() => auth.getAllErrors);
        const errorMessage = computed(() => auth.getErrorMessage);

        const register = async () => {
            try {
                await auth.registerThisData(registerForm);
                await router.push('/');
                alert('Your registration request sent to superadmin.');
            } catch (error) {
                alert(auth.getErrorMessage);
            }
        }

        const goToLoginPage = () => {
            router.push('/');
        }

        return {
            auth,
            errors,
            registerForm,
            errorMessage,
            register,
            goToLoginPage
        }
    },
})
